<template>
  <div class="pb-4">
    <b-container class="pt-2">
      <div class="mb-5 large-title mt-5"> </div>

      <div class="mb-5">
        <input type="text" v-model="reglament.name" class="serchInput w-100" placeholder="  ">
      </div >

      <div class="d-flex ">
        <div class="items">
          <div class="md-title"></div>
          <div class="mt-4 mr-4">
            <div class="cursor-pointer it" :class="{ active: tab===0 }"  @click="tab=0">
              <div class="d-inline-block ml-2 pl-1">  </div>
            </div>
            <div class="cursor-pointer it" :class="{ active: tab===1 }"  @click="tab=1">
              <div class="d-inline-block">1.</div>
              <div class="d-inline-block"> C  </div>
            </div>
            <div class="cursor-pointer it" :class="{ active: tab===2 }"  @click="tab=2">
              <div class="d-inline-block">2.</div>
              <div class="d-inline-block">    </div>
            </div>
            <div class="cursor-pointer it" :class="{ active: tab===3 }"  @click="tab=3">
              <div class="d-inline-block">3.</div>
              <div class="d-inline-block"> ,  </div>
            </div>
            <div class="cursor-pointer it" :class="{ active: tab===4 }"  @click="tab=4">
              <div class="d-inline-block">4.</div>
              <div class="d-inline-block">    </div>
            </div>
            <div class="cursor-pointer it" :class="{ active: tab===5 }"  @click="tab=5">
              <div class="d-inline-block">5.</div>
              <div class="d-inline-block">     </div>
            </div>
            <div v-if="id && currentRevision && revisionId">
              <div class="active mt-5 mb-2">
                <b-form-checkbox
                  class="float-right"
                  id="checkbox-1"
                  v-model="currentRevision.is_marked_solved"
                  name="checkbox-1"
                  :value="true"
                  unchecked-value="not_accepted"
                >
                  
                </b-form-checkbox>
              </div>
              <textarea disabled class="w-100 comment-tx" rows="9" cols="35" v-model="currentRevision.report"></textarea>
            </div>
          </div>
        </div>

        <div class="h600">
          <quillEditor v-model="content"></quillEditor>
        </div>

      </div>



    </b-container>
    <b-container class="text-right"><button @click="saveReglaments" class="mtt-2"></button></b-container>
    <!-- <b-container v-for="user in users" :key="user.id"> -->
    <b-container>
       <!-- <div class="md-title mt-4 mb-4 pt-4">   {{tab}}</div>
        <b-row class="task-item w-100" align-v="start">
          <b-col cols="4" class="justify-content-between d-flex align-items-center">
            <UserPreview />

            <div class="mt-2 u-email small_grey-text"></div>
          </b-col>
          <b-col cols="7" class="mt-2 md-text text-left small_grey-text">
                     
          </b-col>
          <b-col cols="1" class="mt-4 md-text  small_grey-text text-right">
            02.10.2020
          </b-col>
        </b-row> -->
      </b-container>

  </div>
</template>

<script>
// import mainService from '../sevices/mainService'
import { mapGetters } from 'vuex'
// import UserPreview from "../components/UserPreview";
import mainService from '../sevices/mainService'
import 'quill/dist/quill.core.css'
import 'quill/dist/quill.snow.css'
import 'quill/dist/quill.bubble.css'

import { quillEditor } from 'vue-quill-editor'

// element.scrollTop = element.scrollHeight
export default {
  name: 'Reglament',
  components: {
    // UserPreview,
    quillEditor
  },
  props: {
    id: {
      required: false,
      type: String
    },
    revisorId: {
      required: false,
      type: String
    },
    revisionId: {
      required: false,
      type: String
    }
  },
  computed: {
    ...mapGetters([
      'currentUser',
    ])
  },
  data: () => ({
    currentRevision: {},
    revisions: [],
    text: '',
    content: "",
    tab: 0,
    reglament: {
      name: "",
      parts: {
        label: {label: "", comments: []},
        text1: { label: "", text: "" },
        text2: { label: "", text: "" },
        text3: { label: "", text: "" },
        text4: { label: "", text: "" },
        text5: { label: "", text: "" },
      }
    }
  }),
  created() {
    if (this.id) {
      this.getReglament()
        .then(() => {
          if (this.revisorId) {
            this.getRevisionsByUser();
          }
        })
    }
  },
  watch: {
    tab(num, lastNum) {
      this.setContent(num, lastNum)
      if (this.revisionId) {
        this.$nextTick(() => {
          this.setIds();
        })
      }
    },
    "currentRevision.is_marked_solved"(val) {
      this.revisions.find(({id}) => id == this.currentRevision.id).is_marked_solved = val;
    }
  },
  methods: {
    setIds(toShow = false) {
      const spans = document.querySelector(".ql-editor").querySelectorAll('span');
      if (toShow) {
        document.querySelector(".ql-editor").scrollTop = spans[this.currentRevision.secret_id].getBoundingClientRect().top - 400;
        // element.scrollTop = element.scrollHeight
      }
      spans.forEach((span, index) => {
        // index;
        span.addEventListener('click', () => {
          this.currentRevision = this.revisions.find((revision) => {
            const part = this.tab === 0 ? 'label' : "text" + this.tab;
            if (this.tab === 0) {
              return revision.secret_id == index && part == revision.regulation_part;
            }
            return part == revision.regulation_part;
          });
        })
      });
    },
    getRevisionsByUser() {
      mainService.getRevisionsByUser(this.id, this.revisorId)
        .then(({data}) => {
          this.revisions = data.results;
          this.currentRevision = data.results.find(({id}) => id == this.revisionId);
          this.setRevisions();
        })
    },
    setRevisions() {
      this.revisions.forEach(revision => {
        if (revision.regulation_part) {
          const htmltemplate = `<span class="test" id="${revision.secret_id}-comment" style="color: rgb(231, 0, 0);">${revision.default_text}</span>`;
          if (revision.regulation_part == 'label') {
            this.reglament.parts[revision.regulation_part].text = this.reglament.parts[revision.regulation_part].text.replace(revision.default_text, htmltemplate);
          } else {
            this.reglament.parts[revision.regulation_part].text = this.reglament.parts[revision.regulation_part].text.replace(revision.default_text, htmltemplate);
          }
        }
      })

      this.tab = this.currentRevision.regulation_part == "label" ? 0 : this.currentRevision.regulation_part[this.currentRevision.regulation_part.length - 1];
      this.content = this.tab == 0 ? this.reglament.parts.label.text : this.reglament.parts[`text${this.tab }`].text;

      this.$nextTick(() => {
        this.setIds(true);
      })
    },
    saveInfo(num) {
      this.info[num] = this.$refs.vueditor.getContent();
    },
    setContent(num, lastNum) {
      if (lastNum === 0) {
        this.reglament.parts.label.text = this.content;
      } else {
        this.reglament.parts[`text${lastNum}`].text = this.content;
      }

      this.content = this.tab == 0 ? this.reglament.parts.label.text : this.reglament.parts[`text${num}`].text;
    },
    getReglament() {
      return mainService.getReglament(this.id)
        .then(({data}) => {
          this.reglament = data[0];
          this.content = this.tab == 0 ? this.reglament.parts.label.text : this.reglament.parts[`text${this.tab}`].text;
        })
    },
    markRevisions() {
      const revisions = this.revisions.filter(({is_marked_solved}) => is_marked_solved);
      mainService.markRevisions(revisions)
        .then(() => {
          this.$router.go(-1)
        })
    },
    saveReglaments() {
      if (this.tab == 0) {
        this.reglament.parts.label.text = this.content;
      } else {
        this.reglament.parts[`text${this.tab}`].text = this.content;
      }

      if (this.id) {
        mainService.changeReglament(this.reglament)
          .then(() => {
            this.markRevisions();
          })
      }  else {
        mainService.saveReglament(this.reglament)
          .then(() => {
            this.$router.go(-1)
          })
      }
    }
  }
}
</script>

<style scoped lang="scss">
  .quill-editor{
    height: 630px;
  }

  .task-item{
    min-height: 68px;
    border-bottom: 1px solid #D9D9DE;
  }
  .serchInput{
    width: 576px;
    border: 1px solid #0061D9;;
  }
  .h600{
    min-height: 700px;
    padding-bottom: 63px;
  }
  .items{
    min-width: 350px;
  }


  .it{
    font-size: 17px;
    /* // white-space: nowrap; */
    margin-right: 20px;
    margin-top: 30px;
    display: flex;

  }

  .active{
    font-weight: bold !important;
    color:  #0050B2 !important;
  }

  .comment-tx{
    font-size: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 5px;
    outline: none !important;
    min-height: 210px;
  }

  .mtt-2{
    margin-top: 20px;
  }

  .mt-minus{
    transform: translateY(-52px) !important;
    margin-top: 0 !important;
  }
</style>
